.App{
  background-color: #006d75;
  min-height: 50rem;
  width: 30rem;
  margin-top: 5rem;
  margin-left: 45rem;
  margin-right: 15rem;
  max-height: 50rem;
  overflow: auto;
}

#to-do-form input{
  background-color: #13c2c2;
  border: 0;
  width: 300px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  text-align: center;
  margin-left: 20px;
  border-radius: 7px;
  color: white;
  font-size: 20px;
}

#to-do-form input::placeholder{
  color: grey;
  text-align: left;
}
#to-do-form input:focus{
  outline: none;
}

#to-do-form button{
  background-color: #faad14;
  border: 0;
  width: 5rem;
  height: 3rem;
  font-size: 20px;
  border-radius: 7px;
  color: white;
  cursor: pointer;
}


.list{
  color: #fff;
  width: 400px;
  margin: 20px auto;
  height: 42px;
  background-color: #00474f;
  border-radius: 5px;
}
.list p{
  padding: 10px;
  position: relative;
  transition: ease-in-out;
}
.list p input{
  background-color: transparent;
  border: 0;
  color: #fff;
  font-size: 18px;
}
.list p input:focus{
  outline: none;
}
.list p span{
  position: absolute;
  right: 10px;
}
.faicons{
  margin: 0 5px;
  font-size: 15px;
  cursor: pointer;
}
.itemsAnim-enter{
  opacity: 0.01;
}
.itemsAnim-enter .itemsAnim-enter-active{
  opacity: 1;
  transition: opacity 300ms ease-in
}
.itemsAnim-leave{
  opacity: 1;
}
.itemsAnim-leave .itemsAnim-leave-active{
  opacity: 0.01;
  transition: opacity 300ms ease-in
}